import React, { Component } from "react";
import { Link } from "react-router-dom";
import { forgotPassword } from '../../../actions/loginActions'
class ForgotPassword extends Component {
    constructor(props) {
        super(props)
    }
    state = {
        accountEmailId: null,
        emailError: null,
        success: null
    }
    onEmailTextChange = event => {
        this.setState({ accountEmailId: event.target.value, emailError: null })
    }
    onClickToForgot = event => {
        const { accountEmailId } = this.state;
        const { activeProduct } = this.props
        var self = this;
        if (accountEmailId && String(accountEmailId).length > 0) {
            forgotPassword({ emailId: accountEmailId, activeProduct }).then((response) => {
                console.log("onClickToForgot", response)
                self.setState({ success: true })
            })
        }
        else {
            console.log("emailError")
            this.setState({ emailError: true })
        }

    }
    render() {
        const { prefix } = this.props
        const { accountEmailId, emailError, success } = this.state
        return <>
            <div class="loginFormSec registerFormSec formControlMin">
                <div class="loginBlock">
                    <div class="loginInnSec">
                        <div class="loginHead">
                            <h2>Forgot Password</h2>
                            <p>For existing user</p>
                        </div>

                        <p class="text-center"><strong>Enter your email address below to reset your password.</strong></p>

                        <div class="spacer10px"></div>
                        {success ? <div id="flashFlushMessage" class="note note-success">
                            <p>
                                Success! Please check your e-mail to reset your password.	</p>
                        </div> : null}
                        <div class="spacer15px"></div>

                        <div id="AccountForgotpasswordForm" >
                            <div class="form-group labelPosition">
                                <label for="AccountEmailId">Email Id</label>
                                <input name="data[Account][emailId]" class={`form-control placeholder-no-fix ${emailError ? `form-error` : ``}`} maxlength="255" type="email" id="AccountEmailId" value={accountEmailId} onChange={this.onEmailTextChange} /> </div>
                            <div class="form-group">
                                <button class="btn ladda-button spinnerButton" data-style="expand-left" onClick={this.onClickToForgot}><span class="ladda-label changeText">Submit</span></button>
                            </div>
                            <div class="form-group">
                                <p>Take me back to <Link to={`${prefix}accounts/login`} class="btnSignUp">Login</Link></p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    }
}

export default ForgotPassword