import React, { Suspense } from "react";
import { Navigate, Switch, Route, Routes, useNavigate, useParams } from "react-router-dom";
import VerifyEmail from '../View/accounts/verifyEmail'
import InitPage from '../View/init'
const UpdateVerifyEmail = (props) => {
    const params = useParams();
    return <VerifyEmail prefix={props.prefix} params={params} history={props.history} />
}
const LoginBasePage = (props) => {
    const history = useNavigate();
    return (
        <Suspense fallback={<>loading</>}>
            {
                <Routes>
                    <Route exact path="/init" element={<InitPage history={props.history} />} />
                    <Route exact path="/widget/accounts/verifyemail/:id" element={<UpdateVerifyEmail prefix="/widget/" history={props.history} />} />
                    <Route exact path="/accounts/verifyemail/:id" element={<UpdateVerifyEmail prefix="/" history={props.history} />} />
                </Routes>

            }

        </Suspense >
    );
}
export default LoginBasePage