import React, { Component } from "react";
import { Link } from "react-router-dom";
import { connect } from "react-redux";
import IntlTelInput from 'react-intl-tel-input';
import 'react-intl-tel-input/dist/main.css';
import { otherInfoSkip, otherInfoUpate } from '../../../actions/loginActions'

const isValidNumber = (n) => {
    return /^-?[\d.]+(?:e-?\d+)?$/.test(n);
}

class OtherInfo extends Component {
    constructor(props) {
        super(props)
    }

    onInTelLoad = () => {

        var errorMsg = document.querySelector("#error-msg"),
            validMsg = document.querySelector("#valid-msg");
        var errorMap = ["Invalid number", "Invalid country code", "Too short", "Too long", "Invalid number"];
        var submitBtn = document.getElementById('register-submit-btn');
        var contactError = document.getElementById('contact-error');

        var input = document.querySelector("#phone");
        console.log("input", input)
        if (input) {
            var reset = function () {
                input.classList.remove("error");
                errorMsg.innerHTML = "";
                errorMsg.classList.add("hide");
                submitBtn.disabled = false;

            };
            input.addEventListener('blur', function () {
                reset();
                var contactNo = input.value.trim();

                if (contactNo) {

                    if (isNumber(contactNo)) {
                        validMsg.classList.remove("hide");
                        submitBtn.disabled = false;
                        contactError.style.display = "none";

                    } else {
                        input.classList.add("error");
                        // var errorCode = iti.getValidationError();
                        // errorMsg.innerHTML = errorMap[errorCode];
                        errorMsg.classList.remove("hide");
                        submitBtn.disabled = true;
                        contactError.style.display = "none";

                    }

                }
            });
            input.addEventListener('change', reset);
            input.addEventListener('keyup', reset);
            function isNumber(n) {
                return /^-?[\d.]+(?:e-?\d+)?$/.test(n);
            }
        }
    }
    state = {
        accountCompanyName: null,
        acnError: null,
        phoneError: null,
        phone: null,
        country_code: null
    }
    onACNChange = event => this.setState({ accountCompanyName: event.target.value, acnError: null })
    onClickToSkip = event => {
        const { history } = this.props
        this.props.otherInfoSkip(history)
    }
    onClickToFinish = event => {
        const { history, signUpData } = this.props

        const { accountCompanyName, phone, country_code } = this.state;
        if (accountCompanyName && String(accountCompanyName).length > 0 && phone && country_code) {
            this.props.otherInfoUpate({ userCode: signUpData.responseData.userCode, organization: accountCompanyName, contact_no: phone, country_code: country_code }, history, signUpData.responseData.activeProduct)
        }
        else {
            this.props.otherInfoSkip(history)
            // if (!accountCompanyName || String(accountCompanyName).length == 0) this.setState({ acnError: true })
            // if (!phone || String(phone).length == 0 || !country_code || String(country_code).length == 0) {
            //     var errorMsg = document.querySelector("#error-msg")
            //     errorMsg.innerHTML = "Please enter number";
            //     errorMsg.classList.remove("hide");
            // }

        }
    }
    componentDidMount() {
        this.onInTelLoad()
    }

    updatePhoneNumber1 = (isValid, value, selectedCountryData, fullNumber, extension) => {
        console.log("updatePhoneNumber - updatePhoneNumber 111", isValid, value, selectedCountryData, fullNumber, extension)

        var errorMsg = document.querySelector("#error-msg"),
            validMsg = document.querySelector("#valid-msg");
        var submitBtn = document.getElementById('register-submit-btn');
        var contactError = document.getElementById('contact-error');
        var input = document.querySelector("#phone");
        if (value) {
            if (isValid && isValidNumber(value)) {
                validMsg.classList.remove("hide");
                validMsg.innerHTML = " Valid";
                submitBtn.disabled = false;
                contactError.style.display = "none";
                this.setState({ phone: value, country_code: selectedCountryData.dialCode })

            } else {
                input.classList.add("error");
                // var errorCode = iti.getValidationError();
                errorMsg.innerHTML = "Invalid number";
                errorMsg.classList.remove("hide");
                validMsg.innerHTML = "";
                validMsg.classList.add("hide");
                submitBtn.disabled = true;
                contactError.style.display = "none";

            }

        }


    }

    render() {
        const { prefix } = this.props
        const { accountCompanyName, acnError } = this.state;

        return <>
            <div class="loginFormSec registerFormSec formControlMin">
                <div class="loginWithSocials">
                    <div class="loginHead">
                        <h2>You're nearly there </h2>
                    </div>
                </div>
                <div class="loginBlock">
                    <div class="loginInnSec">
                        <div id="AccountOtherinfoForm">
                            <div class="row">
                                <div class="col-sm-12">
                                    <div class="form-group labelPosition">
                                        <label for="AccountCompanyName">Company Name</label>
                                        <input name="data[Account][company_name]" class={`form-control placeholder-no-fix ${acnError ? ` form-error` : ``}`} type="text" value={accountCompanyName} id="AccountCompanyName" data-factors-input-id="form-0.field-0" onChange={this.onACNChange} />
                                        {acnError ? <span class="errorMessage">Company Name is required.</span> : null}
                                    </div>
                                    <div class="form-group labelPosition">
                                        <input type="hidden" name="data[Account][country_code]" id="country_code" class="form-control placeholder-no-fix" val="1" />
                                        <label for="phone">Contact No</label>
                                        <IntlTelInput fieldId="phone"
                                            style={{ width: `100%` }}
                                            preferredCountries={['IN']}
                                            onPhoneNumberBlur={this.updatePhoneNumber1}
                                            containerClassName="intl-tel-input"
                                            inputClassName="form-control placeholder-no-fix" autocomplete="off" data-intl-tel-input-id="0" placeholder="9987654321" data-factors-input-id="form-0.field-1"

                                        />

                                        <div id="contact-error">
                                        </div>
                                        <div id="error-msg" class="hide" style={{ left: 0, color: '#d41220', font: "400 12px/25px 'open sans', sans-serif" }}></div>
                                        <div id="valid-msg" class="hide" style={{ left: 0, color: 'green', font: "400 12px/25px 'open sans', sans-serif" }}></div>

                                    </div>
                                </div>
                                <div class="col-sm-12">
                                    <div style={{ clear: 'both' }}></div>
                                    <div class="form-group">
                                        <button onClick={this.onClickToFinish} id="register-submit-btn" data-style="expand-left" class="btn ladda-button spinnerButton">
                                            Finish
                        </button>
                                    </div>
                                    <div class="skip_btn_wrap">
                                        <p> <a class="skip_btn" onClick={this.onClickToSkip} style={{ cursor: 'pointer' }}>Skip</a></p>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="clear"></div>
            </div>
        </>
    }
}
const mapStateToProps = (state) => {
    return {
        signUpData: state.signUp.signUpData
    }
}
const mapDispatchToProps = dispatch => {
    return {
        otherInfoSkip: (history) => dispatch(otherInfoSkip(history)),
        otherInfoUpate: (data, history, redirect) => dispatch(otherInfoUpate(data, history, redirect)),


    }
}
export default connect(mapStateToProps, mapDispatchToProps)(OtherInfo);