import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import { Aside } from "./Aside/Aside";
import { Header } from "./Header/Header";

export function Layout(props) {
    const { children } = props;

    const [width, setWidth] = useState(window.innerWidth);
    useEffect(() => {
        function handleResize() {
            setWidth(window.innerWidth);
        }
        window.addEventListener("resize", handleResize);
        return () => window.removeEventListener("resize", handleResize);
    }, [width]);


    const [isActiveMobileMenu, setActiveMobileMenu] = useState("false");
    const MobileTopbarMenu = () => {
        setActiveMobileMenu(!isActiveMobileMenu);
        if (isActiveMobileMenu) {
            document.body.classList.add('topbar-mobile-on');
        } else {
            document.body.classList.remove('topbar-mobile-on');
        }
    };

    const [sidebarMobileMenu, setSidebarMobileMenu] = useState(false);
    const ActiveSidebarMobileMenu = () => {
        setSidebarMobileMenu(!sidebarMobileMenu);
    };
    const sidebarToggle = width < 992 ? sidebarMobileMenu : true



    const [isActiveProAside, setProAsideMobileMenu] = useState(false);
    const ProductMenuAside = () => {
        setProAsideMobileMenu(!isActiveProAside);
    };

    return <>
        <div id="kt_header_mobile" className="header-mobile align-items-center header-mobile-fixed">
            <a href="" className="mobile-brand-logo">
                <img width="img-fluid" alt="Logo" src="https://app.taggbox.com/widget/assets/media/logos/taggbox-logo.svg" />
            </a>
            <div className="d-flex align-items-center">
                <button type="button" className="btn p-0 custom_burger_icon" id="kt_aside_mobile_toggle" onClick={ActiveSidebarMobileMenu}>
                    <span></span>
                    <span></span>
                    <span></span>
                </button>
                <button type="button" className="btn p-0 burger-icon ml-4 pro_head_icon" id="kt_header_mobile_toggle" onClick={ProductMenuAside}>
                    <img className="img-fluid d-block" src="https://app.taggbox.com/widget/assets/media/products-icon/widget.svg" />
                </button>
                <button type="button" className="btn btn-hover-text-primary p-0 ml-2" id="kt_header_mobile_topbar_toggle" onClick={MobileTopbarMenu}>
                    <span className="svg-icon svg-icon-xl">
                        <svg xmlns="http://www.w3.org/2000/svg" width="24px" height="24px" viewBox="0 0 24 24" version="1.1">
                            <g stroke="none" strokeWidth="1" fill="none" fillRule="evenodd">
                                <polygon points="0 0 24 0 24 24 0 24"></polygon>
                                <path d="M12,11 C9.790861,11 8,9.209139 8,7 C8,4.790861 9.790861,3 12,3 C14.209139,3 16,4.790861 16,7 C16,9.209139 14.209139,11 12,11 Z" fill="#000000" fillRule="nonzero" opacity="0.3"></path>
                                <path d="M3.00065168,20.1992055 C3.38825852,15.4265159 7.26191235,13 11.9833413,13 C16.7712164,13 20.7048837,15.2931929 20.9979143,20.2 C21.0095879,20.3954741 20.9979143,21 20.2466999,21 C16.541124,21 11.0347247,21 3.72750223,21 C3.47671215,21 2.97953825,20.45918 3.00065168,20.1992055 Z" fill="#000000" fillRule="nonzero"></path>
                            </g>
                        </svg>
                    </span>
                </button>
            </div>
        </div>
        <div className="d-flex flex-column flex-root">
            <div className="d-flex flex-column-fluid page">
                <Header isActiveProAside={isActiveProAside} isActiveProAsideFun={setProAsideMobileMenu} windowWidth={width} />
                <Aside sidebarMobileMenu={sidebarToggle} windowWidth={width} />
                <div className="d-flex flex-column flex-row-fluid wrapper" id="kt_wrapper">
                    <div className="d-flex flex-column-fluid">
                        <div className="content  d-flex flex-column flex-column-fluid" id="kt_content">
                            <h2 className="text-center mt-10 w-100 pt-10">Body Content {width}</h2>
                            {children}
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </>
}
