import React, { Suspense, useState, useEffect } from "react";
import { Navigate, Switch, Route, Routes, useNavigate, useParams } from "react-router-dom";
import Login from '../View/accounts/login'
import SignUp from '../View/accounts/signUp'
import ForgotPassword from '../View/accounts/forgotPassword'
import ResetPassword from '../View/accounts/resetPassword'
import OtherInfo from '../View/accounts/otherinfo'



const UpdateResetPassword = (props) => {
    const params = useParams();
    return <ResetPassword prefix={props.prefix} params={params} />
}


const LoginBasePage = (props) => {

    const params = useParams();
    console.log("sadasdsd")
    return (
        <Suspense fallback={<>loading</>}>
            {
                <Routes>
                    <Route exact path="/" element={<Login prefix="/" history={props.history} />} />
                    <Route exact path="/accounts/register" element={<SignUp prefix="/" history={props.history} />} />
                    <Route exact path="/accounts/login" element={<Login prefix="/" history={props.history} />} />
                    <Route exact path="/accounts/forgotpassword" element={<ForgotPassword prefix="/" activeProduct="0" />} />
                    <Route exact path="/accounts/resetpassword/:userId/:token" element={<UpdateResetPassword prefix="/" />} />
                    <Route exact path="/widget/accounts/forgotpassword" element={<ForgotPassword prefix="/widget/" activeProduct="6" />} />
                    <Route exact path="/widget/accounts/resetpassword/:userId/:token" element={<UpdateResetPassword prefix="/widget/" />} />
                    <Route exact path="/widget/accounts/register" element={<SignUp prefix="/widget/" history={props.history} />} />
                    <Route exact path="/widget/accounts/login" element={<Login prefix="/widget/" history={props.history} />} />
                    <Route exact path="/accounts/otherinfo" element={<OtherInfo prefix="/" history={props.history} />} />
                    <Route exact path="/widget/accounts/otherinfo" element={<OtherInfo prefix="/widget/" history={props.history} />} />


                </Routes>

            }

        </Suspense >
    );
}
export default LoginBasePage