import axios from 'axios';
import HttpClient from "./http-client";
import { GET_INIT_PRODUCT, SELECT_PRODUCT, SIGN_UP_VERIFICATION, CREATE_WALL } from './api'
import { USER_TOKEN, USER_AUTHENTICATED } from '../redux/reducersKeys'


export const getInItProduct = () => {
    return new HttpClient().get(GET_INIT_PRODUCT)
}

export const selectProduct = (data, history) => {
    const { productId } = data
    console.log("data selectProduct", data)
    return (dispatch) => {
        new HttpClient().post(SELECT_PRODUCT, data).then((response) => {
            const { responseData } = response.data
            console.log("responseData selectProduct", responseData)
            dispatch({ type: USER_AUTHENTICATED, payload: responseData });
            if (String(productId) == "0") history("/widget/walls/home?id=widget")
            else if (String(productId) == "6") history("/widget/walls/home?id=widget")
            else if (String(productId) == "4") history("/display/walls/home?id=display")
        })
    }
}
export const signUpVerified = (token, history) => {
    return (dispatch) => {
        new HttpClient().post(`${SIGN_UP_VERIFICATION}/${token}`).then((response) => {
            const { responseData } = response.data
            dispatch({ type: USER_TOKEN, payload: responseData });
            localStorage.setItem('token', responseData.token)
            new HttpClient().post(`${CREATE_WALL}`, {}, {
                headers: { 'authorization': `Bearer ${responseData.token}` },
            }).then((wallResponse) => {
                if (responseData.activeProduct == "0") history("/accounts/otherinfo")
                else if (responseData.activeProduct == "6") history("/widget/walls/home")
                else if (responseData.activeProduct == "4") history("/display/walls/home")
                dispatch({ type: USER_AUTHENTICATED, payload: responseData });
            })
        })
    }
}