import React, { Component } from "react";
import { Navigate, Switch, Route, Routes, useNavigate, useParams } from "react-router-dom";
import { shallowEqual, useSelector } from "react-redux";
import { Layout, LoginLayout } from "../layout";
import BasePage from "./basePage"
import LoginBasePage from "./loginBasePage"
import NoBasePage from "./noBasePage"

const RoutesExtends = (props) => {
    const history = useNavigate();
    const params = useParams();
    const { signUpData, authenticated } = useSelector(
        ({ signUp, authenticated }) => ({
            signUpData: signUp.signUpData,
            authenticated: authenticated.userData
        }),
        shallowEqual
    );
    console.log("signUpData, authenticated", signUpData, authenticated)
    return authenticated && Object.keys(authenticated).length > 0 ? <Layout>
        <BasePage history={history} />
    </Layout> : signUpData && Object.keys(signUpData).length > 0 && signUpData.redirect != "other" ? <><NoBasePage history={history} /></> : window.location.href.includes("verifyemail") ? <><NoBasePage history={history} /></> :

        <LoginLayout>
            <LoginBasePage history={history} />
        </LoginLayout>
}


export default RoutesExtends;