import axios from 'axios';
import HttpClient from "./http-client";
import { LOGIN, FORGOT_PASSWORD, RESET_PASSWORD, IP_INFO, SIGN_UP, OTHER_INFO } from './api'
import { SIGN_UP_KEY, OTHER_SKIP } from '../redux/reducersKeys'


export const loginUser = (data) => {
    return new HttpClient().post(LOGIN, data)
}

export const forgotPassword = (data) => {
    return new HttpClient().post(FORGOT_PASSWORD, data)
}
export const resetPasswords = (data) => {
    return new HttpClient().post(RESET_PASSWORD, data)
}
export const getIPDetails = () => {
    return new HttpClient().get(IP_INFO)
}

export const userSignUp = (data, history, responseCallback) => {
    return (dispatch) => {
        new HttpClient().post(SIGN_UP, data).then((resposne) => {
            console.log("resposne", resposne)
            const { responseData } = resposne.data;
            if (responseData.activeProduct == "0") history("/accounts/otherinfo")
            else if (responseData.activeProduct == "6") history("/widget/walls/home?id=widget")
            else if (responseData.activeProduct == "4") history("/display/walls/home?id=display")
            dispatch({ type: SIGN_UP_KEY, payload: { ...resposne.data, redirect: "other" } });

        }).catch((ex) => {
            const { data, status } = ex.response
            if (status == 401) {
                responseCallback(data)
            }
            console.log("responseCallback", ex)
        })
    }
}
export const otherInfoSkip = (history) => {
    return (dispatch) => {
        dispatch({ type: OTHER_SKIP, payload: "init" });
        history("/init")
    }
}
export const otherInfoUpate = (data, history, redirect) => {
    return (dispatch) => {
        new HttpClient().post(OTHER_INFO, data).then((resposne) => {
            const { responseData } = resposne.data;
            if (responseData.activeProduct == "0") history("/init")
            else if (responseData.activeProduct == "6") history("/widget/walls/home?id=widget")
            else if (responseData.activeProduct == "4") history("/display/walls/home?id=display")
            dispatch({ type: SIGN_UP_KEY, payload: { ...resposne.data, redirect: "init" } });
        })
    }
}